import React, { Component } from 'react'
import { NavBar, Icon } from 'antd-mobile';
import '../../css/WoDe/Xuexijindu.css';

export default class Xuexijindu extends Component {
    render() {
        return (
            <div>
                <div className="wode_back"></div>
                <NavBar mode="dark" icon={<Icon type="left" onClick={()=>{this.props.history.push('/home/wode')}}/>} style={{background:'#617ca6',color:'#fff'}}>   </NavBar>
                <div className="xxjd_box">
                    <i className={'iconfont icon-xuexijindu xxjd_tubiao'}></i>
                    <span className="xxjd_text"></span>
                    <div className="xxjd_text_l">
                        <span></span>
                        <span>6</span><span>/100</span>
                    </div>
                </div>
                <div className="xxjd_big">
                    <div>
                        <span className="left_block"></span>
                        <span></span>
                    </div>
                    <div className="container">
                        <div className='container_text'>
                            <span >:</span>
                            <span>3</span>
                            <span className='container_text1'>:</span>
                            <span>3</span>
                        </div>
                    </div>
                </div>
                <div className="xxjd_big">
                    <div>
                        <span className="left_block"></span>
                        <span></span>
                    </div>
                    <div className="container">
                        <div className='container_text'>
                            <span >:</span>
                            <span>3</span>
                            <span className='container_text1'>:</span>
                            <span>3</span>
                        </div>
                    </div>
                </div>
                <div className="xxjd_big">
                    <div>
                        <span className="left_block"></span>
                        <span></span>
                    </div>
                    <div className="container">
                        <div className='container_text'>
                            <span >:</span>
                            <span>3</span>
                            {/* <span className='container_text1'>:</span>
                            <span>3</span> */}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
