import React, { Component } from 'react'
// import Idiomscon from './Idiomscon';
import {BrowserRouter as Router,Link} from "react-router-dom"
import {NavBar,SearchBar,TextareaItem,Icon} from 'antd-mobile';
import "../../css/ChengYu/xcy.css"
export default class LearnIdioms extends Component {
    constructor(){
        super();
        this.content={
            chengyu:"",
            shiyi:"",
            biyu:"",
            liju:"",
            image:"",
            story:':"!!"'
        }
        this.state={
            num:1
        }
    }
    minus=(e)=>{
        this.setState((state)=>{
            if(state.num==1){
                return{
                    num:1
                }
            }
            return{
                num:--state.num
            }
        })
    }
    adds=()=>{
        
        this.setState((state)=>{
            return{
                num:++state.num
            }
        })
    }
    change=(e)=>{
        e.target.className=(e.target.className=="iconfont icon-xingxing black")?"iconfont icon-xingxing1 yello":"iconfont icon-xingxing black"
    }
    render() {
        return (
            <div>
                <NavBar
                    icon={<Icon type="left" onClick={()=>{this.props.history.push('/home/chengyu')}} />}
                    onLeftClick={() => console.log('onLeftClick')}
                    style={{backgroundColor:"#617ca6"}}
                    >  </NavBar>
                
                <div className="learnidiombody"> 
                    
                    <div className="learnidiomcon">
                        <SearchBar
                            placeholder=""
                            onSubmit={value => console.log(value, 'onSubmit')}
                            onClear={value => console.log(value, 'onClear')}
                            onFocus={() => console.log('onFocus')}
                            onBlur={() => console.log('onBlur')}
                            onCancel={() => console.log('onCancel')}
                            onChange={this.onChange}
                            style={{height:"2rem"}}
                        />
                    <div>
                        <div className="orange"><span> {this.state.num}</span><span> / </span><span>100 </span></div>
                        <div onClick={this.change} className="iconfont icon-xingxing black"></div>
                        <div className="idiomcon">
                            <div><img className="learnimg" src={require("../../images/bg3.jpg") }/></div>
                            <div><span>: </span><span>{this.content.chengyu}</span><span className="iconfont icon-laba1" style={{fontSize:22,color:"#617ca6",marginLeft:"10%"}}></span></div>
                            <div><span>: </span>{this.content.shiyi}</div>
                            <div><span>: </span>{this.content.biyu}</div>
                            <div><span>: </span>{this.content.liju}</div>
                            <img src={this.content.image}/>
                            <textarea value={this.content.story}></textarea>
                        </div>
                    </div>   
                </div>
               
               <Router>
                   <div className="idiomsleft" ><Link onClick={this.minus} className="iconfont icon-ico_leftarrow"></Link></div>
                   <div className="idiomsright" onClick={this.adds}><Link  className="iconfont icon-ico_leftarrow"></Link></div>
               </Router>
               
            </div>
            </div>
        )
    }
}
